<template>
  <div class="pos">
    <div>
      <el-row class="pos-box">
        <el-col :span="7" class="pos-order" id="order-list">
          <el-tabs>
            <el-tab-pane label="点餐">
              <el-table :data="tableData" border style="width: 100%">
                <el-table-column prop="goodsName" label="商品"></el-table-column>
                <el-table-column prop="count" label="量" width="65"></el-table-column>
                <el-table-column prop="price" label="金额" width="75"></el-table-column>
                <el-table-column label="操作" width="100" fixed="right">
                  <template scope="scope">
                    <el-button type="text" size="small" @click="addOrderList(scope.row)">添加</el-button>
                    <el-button type="text" size="small" @click ="delSingleGoods(scope.row)">删除</el-button>
                  </template>
                </el-table-column>
              </el-table>

              <div class="todoList">
                数量：{{totalCount}}  &nbsp; 金额:{{totalMoney}}￥
              </div>
              <div class="div-btn">
                <el-button type="warning">挂单</el-button>
                <el-button type="danger" @click="delAllGoods()">删除</el-button>
                <el-button type="success" @click="checkout()">结账</el-button>
              </div>
            </el-tab-pane>
            <el-tab-pane label="挂单">
              挂单
            </el-tab-pane>
            <el-tab-pane label="外卖">
              外卖
            </el-tab-pane>
          </el-tabs>
        </el-col>
        <el-col :span="17">
          <div class="often-goods">
            <div class="title">常用商品</div>
            <div class="often-goods-list">
              <ul>
                <li v-for="item in oftenGoods" @click="addOrderList(item)">
                  <span>{{item.goodsName}}</span>
                  <span class="o-price">￥{{item.price}}元</span>
                </li>
              </ul>
            </div>

            <div class="goods-type">
              <el-tabs>
                <el-tab-pane label="汉堡">
                  <ul class='cookList'>
                    <li v-for="item in type0Goods" @click="addOrderList(item)">
                      <span class="foodImg"><img :src="item.goodsImg" width="100%"></span>
                      <span class="foodName">{{item.goodsName}}</span>
                      <span class="foodPrice">￥{{item.price}}元</span>
                    </li>
                  </ul>
                </el-tab-pane>
                <el-tab-pane label="小食">
                  <ul class='cookList'>
                    <li v-for="item in type1Goods" @click="addOrderList(item)">
                      <span class="foodImg"><img :src="item.goodsImg" width="100%"></span>
                      <span class="foodName">{{item.goodsName}}</span>
                      <span class="foodPrice">￥{{item.price}}元</span>
                    </li>
                  </ul>
                </el-tab-pane>
                <el-tab-pane label="饮料">
                  <ul class='cookList'>
                    <li v-for="item in type2Goods" @click="addOrderList(item)">
                      <span class="foodImg"><img :src="item.goodsImg" width="100%"></span>
                      <span class="foodName">{{item.goodsName}}</span>
                      <span class="foodPrice">￥{{item.price}}元</span>
                    </li>
                  </ul>
                </el-tab-pane>
                <el-tab-pane label="套餐">
                  <ul class='cookList'>
                    <li v-for="item in type3Goods" @click="addOrderList(item)">
                      <span class="foodImg"><img :src="item.goodsImg" width="100%"></span>
                      <span class="foodName">{{item.goodsName}}</span>
                      <span class="foodPrice">￥{{item.price}}元</span>
                    </li>
                  </ul>
                </el-tab-pane>
              </el-tabs>
            </div>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
  import axios from 'axios';
  export default {
    name: 'Pos',
    data(){
      return{
        tableData: [],
        oftenGoods:[],
        type0Goods:[],
        type1Goods:[],
        type2Goods:[],
        type3Goods:[],
        totalCount:0,
        totalMoney:0
      }
    },
    created:function(){
        axios.get('https://www.easy-mock.com/mock/5969b3d21215f31eb57e7b50/pos/shops')
          .then(response=>{
//              console.log(response.data);
              this.oftenGoods = response.data;
          })
          .catch(error=>{
              console.log(error)
          });
        axios.get('https://www.easy-mock.com/mock/5969b3d21215f31eb57e7b50/pos/typeGoods')
          .then(response=>{
              this.type0Goods = response.data[0];
              this.type1Goods = response.data[1];
              this.type2Goods = response.data[2];
              this.type3Goods = response.data[3];

          })
          .catch(error=>{
              console.log(error)
          })
    },
    mounted:function () {
      var orderHeight=document.body.clientHeight;
      document.getElementById("order-list").style.height=orderHeight+'px';
    },
    //点击右侧商品添加到左侧列表中
    methods:{
      //添加订单列表的方法
      addOrderList(goods){

        //判断列表中是否存在商品
        let isHave = false;

//        for(let i = 0; i<this.tableData.length;i++){
////            console.log(this.tableData[i]);
//            if(this.tableData[i].goodsId == goods.goodsId){
//                isHave = true;
//            }
//        }

        for(let item of this.tableData){
            if(item.goodsId == goods.goodsId){
              isHave = true;
            }
        }

        //添加商品到左侧
        if(isHave){
            let arr = this.tableData.filter(o =>o.goodsId == goods.goodsId);
            arr[0].count++;
        }else{
            let newGodds = {goodsId:goods.goodsId,goodsName:goods.goodsName,price:goods.price,count:1};
            this.tableData.push(newGodds)
        }
        //计算总的金额和数量
        this.getAllMoney()
      },
      //删除单个商品
      delSingleGoods(goods){
          //操作数据
        this.tableData = this.tableData.filter(o=> o.goodsId != goods.goodsId);
        this.getAllMoney()
      },
      //删除全部商品
      delAllGoods(){
        if(this.totalCount != 0){
          this.totalMoney = 0;
          this.totalCount = 0;
          this.tableData = [];
        }else{
          this.$message.error('没有删除的内容！');
        }
      },
      //汇总总的金额和数量
      getAllMoney(){
          this.totalMoney = 0;
          this.totalCount = 0;
          if(this.tableData !=0){
            this.tableData.forEach((element)=>{
              this.totalCount += element.count;
              this.totalMoney = this.totalMoney + (element.price*element.count)
            })
          }
      },
      //结账
      checkout() {
        if (this.totalCount!=0) {
          this.tableData = [];
          this.totalCount = 0;
          this.totalMoney = 0;
          this.$message({
            message: '结账成功!',
            type: 'success'
          });
        }else{
          this.$message.error('不能空结！');
        }
      }
    }


  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .pos-order{
    background-color: #f9fafc;
    border-right: 1px solid #c0ccda;
    height:100%;
  }
  .div-btn{
    margin-top: 10px;
  }
  .title{
    height:20px;
    border-bottom:1px solid #d3dce6;
    background-color: #f9fafc;
    padding: 10px;
  }
  .often-goods-list ul li{
    list-style: none;
    float:left;
    border:1px solid #E5E9F2;
    padding:10px;
    margin:5px;
    background-color:#fff;
    cursor: pointer;
  }
  .o-price{
    color:#58B7FF;
  }
  .goods-type{
    clear: both;
  }
  .cookList li{
    list-style: none;
    width:23%;
    border:1px solid #E5E9F2;
    height: auot;
    overflow: hidden;
    background-color:#fff;
    padding: 2px;
    float:left;
    margin: 2px;

  }
  .cookList li span{

    display: block;
    float:left;
  }
  .foodImg{
    width: 40%;
  }
  .foodName{
    font-size: 18px;
    padding-left: 10px;
    color:brown;

  }
  .foodPrice{
    font-size: 16px;
    padding-left: 10px;
    padding-top:10px;
  }
  .todoList{
    border-bottom: 1px solid #dfe6ec;
    background: #fff;
    padding: 10px;
  }
</style>



